import React from "react";
import {connect} from 'react-redux';
import { bindActionCreators } from 'redux';
import { fetchWalletAction } from "../../redux/actions/wallet";
import { withRouter } from 'react-router-dom';
import styled from "styled-components";

const style = {
  wrapper: {
    display: 'block'
  },
  label: {
    marginLeft: "0.5rem",

  },
  icon: {
    fontSize: "1.5rem",
    verticalAlign: "middle"
  }
}

const WalletWrapper = styled("div")(style.wrapper)
const WalletLabel = styled("span")(style.label)
const WalletIcon = styled("i")(style.icon)

class WalletNavItem extends React.Component {
  componentDidMount = () => {
    this.props.fetchWalletAction();
  }


  render () {
    let { balance, status } = this.props;
    return (
      <WalletWrapper>
        <WalletIcon className="now-ui-icons business_money-coins" />
        <WalletLabel>Wallet Balance: {balance}</WalletLabel>
      </WalletWrapper>
    )
  }
}



const mapStateToProps = state => {
	return {
      balance: state.fetchWalletStatus.balance,
      status: state.fetchWalletStatus.status,
      statement: state.fetchWalletStatus.statement,
	  };
  };
  
  const mapDispatchToProps = dispatch => {
    return {
      fetchWalletAction: bindActionCreators(fetchWalletAction, dispatch),
    };
  };
  
  const connector = connect(mapStateToProps, mapDispatchToProps);
  
  export default connector(withRouter(WalletNavItem));


 